<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/bindPhone.png" alt="">
            <div class="curatorText">
                <img src="../../../images/curatorText01.png" alt="">
            </div>
        </div>
        <div class="content">
            <div class="inject">
                <p style="border-bottom:1px solid #CBCBCB">
                    <input type="number" name="" placeholder="请输入您的手机号码" maxlength="11" autofocus v-model="phoneNumber">
                </p>
                <p class="code">
                    <input type="text" name="" value="" placeholder="请输入验证码" maxlength="6">
                    <span class="receiveCode">获取验证码</span>
                </p>

                <!--  -->
            </div>
            <p><span class="next" @click="bindPhone" :class="{disable: !seen}">绑定手机</span></p>

        </div>
        <footer-text></footer-text>

    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
        seen: false,
        phoneNumber:''
      }
  },
  methods:{
      bindPhone:function(){
        let phoneNumber = this.phoneNumber
        console.log(phoneNumber)
        // if(){
        //
        // }
          this.$router.push({path:'/becomeCurator/creatAdminAccount'})
          //返回后台数据方法待添加
      },
  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        min-height: 16rem;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 5rem;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
        .curatorText{
            position: absolute;
            width:84%;
            bottom:-24%;
            left:8%;
            margin:0 auto;
            border-radius: 10px;
            background:#fff;
            text-align: center;
            img{
                width:96%;
                margin: 4% 0;
            }
        }
    }
    .content{
        text-align: center;
        margin-top: 18%;
        .inject{
            width: 80%;
            height:3rem;

            border:1px solid #CBCBCB;
            border-radius: 6px;
            margin: 0 auto;
            margin-bottom:8%;
            background: #fff;
            p{
                position: relative;
                height:50%;
                box-sizing:border-box;
                input{
                    display: inline-block;
                    position: absolute;
                    top:0;
                    left:5%;
                    width:90%;
                    height:100%;
                    border:0;
                    outline:none;
                }
            }
            .code{
                position:relative;
                .receiveCode{
                    position: absolute;
                    display: inline-block;
                    font-size:.3rem;
                    border:1px solid #EE8F45;
                    border-radius: 6px;
                    color:#EE8F45;
                    bottom:18%;
                    right:5%;
                    width:24%;
                    padding:1% 2%;
                }
            }
        }
         .next{
             width:80%;
             height:1.4rem;
             color:#fff;
             font-size:.5rem;
             line-height: 1.4rem;
             display: inline-block;
             background-color: #EE8F45;
             border-radius: 6px;
        }
        .disable{
          background:rgba(0, 0, 0, 0.2);
        }

    }
</style>



